
function qcharm_url() {

    //var url = "http://localhost:8081/qcharm/"
    var url = window.location.href + "/qcharm/"
    console.log(url)

    return url
}

function packRequest(request_body){
    var req_body = {
        method: 'POST',
        mode:'cors',
        headers: {
            'Content-Type': 'application/json'
        },
        body: JSON.stringify(request_body)
    }
    return req_body;
}

export async function copy(request_body) {
    let startTime = Date.now();

    console.log("copy :", request_body);
    var response = await fetch(qcharm_url() + "copy_new", packRequest(request_body));
    await response;
    let responseTime = Date.now() - startTime;

    var body = response.json();
    document.getElementById('responseTime').innerHTML = "Response Time: " + responseTime + "ms";
    console.log(body);
    return body
}

