export async function getPrevLines(request_body) {
    console.log("getPrevLines :", request_body);
    request_body['lineNumber'] = request_body['lineNumber'] - 1
    let startTime = Date.now();
    let response = await fetch(qcharm_url() + "get_prev_lines_new", packRequest(request_body));
    let endTime = Date.now();
    let responseTime = endTime-startTime;
    document.getElementById('responseTime').innerHTML = "Response Time: " + responseTime + "ms";
    //NOTE: reponse is a promise and it serves only once
    // hence two response.json() call will given some error
    var body = await response.json();
    return body;
}

