export async function load(request_body, showErrorMsg) {
    // FIXME: multiple load_file versions
    console.log("load :", request_body);
    let startTime = Date.now();
    let response = await fetch(qcharm_url() + "load_file_new", packRequest(request_body))
    let responseTime = Date.now() - startTime;
    document.getElementById('responseTime').innerHTML = "Response Time: " + responseTime + "ms";
    //NOTE: reponse is a promise and it serves only once
    // hence two response.json() call will given some error
    var body = await response.json();
    if(body.status != null && body.status !== 200) {
        return displayError(body, showErrorMsg);
    }
    return body;
}

