export async function replace(request_body, showErrorMsg){
    // Sends the term to be replaced
    // Expects the page after the term has been replaced. !!RERENDERING REQUIRED
    let startTime = Date.now();
    let body = await fetch(qcharm_url() + "replace", packRequest(request_body));
    let endTime = Date.now();
    let responseTime = endTime-startTime;
    document.getElementById('responseTime').innerHTML = "Response Time: " + responseTime + "ms";
    if(body.status != null && body.status !== 200) {
        return displayError(body, showErrorMsg);
    } else {
        console.log(body.json());
    }
}

export async function execute(filename, inputs, language) {
    // Sends nothing
    // Expects the page after the undo operation. !!RERENDERING REQUIRED
    var request_body = {
        'fileName' : filename,
        'inputs': inputs,
        'language': language
    }
    let startTime = Date.now();
    let response = await fetch(qcharm_url() + "/run_file", packRequest(request_body))
    var body = await response.json()
    let responseTime = Date.now() - startTime;
    document.getElementById('responseTime').innerHTML = "Response Time: " + responseTime + "ms";
    console.log(body);

    return body.output;
}
