import React from 'react';
import {search, searchReplace, jump, displayError, execute} from './FetchCalls';
import {Nav, Button, Dropdown, DropdownButton, InputGroup, FormControl, Form} from 'react-bootstrap';

class Navbar extends React.Component{
    constructor(props){
        super(props);
        this.handleFindEvent = this.handleFindEvent.bind(this);
        this.handleReplaceEvent = this.handleReplaceEvent.bind(this);
        this.handleFiles = this.handleFiles.bind(this);
        this.getFileData = this.getFileData.bind(this);
        this.jumpToSearchLine = this.jumpToSearchLine.bind(this);
        this.setCaretLoc = this.setCaretLoc.bind(this);
        this.replaceToggle = this.replaceToggle.bind(this);
        this.handleJumpEvent = this.handleJumpEvent.bind(this);
        this.findAndReplaceInterface = this.findAndReplaceInterface.bind(this);
    }

    uploadFromFile = evt => {
        document.getElementById('myfile').click();
    }

    runCode = () => {
        this.props.state.editHandler.editBackEnd();
        let inputs = document.querySelector(".custom-input").value;
        console.log(inputs);
        let self = this;
        execute(this.props.state.fileName, inputs.split('\n'), this.props.state.language).then((result) => {
            self.props.setResult(result);
        });
    }

    getCaretLoc(data=document.querySelector(".code-input").value, selection=document.querySelector(".code-input").selectionEnd){
        var row = 0;
        var col = 0;
        data = data.split("\n");
        var prevLen = 0;
        while(row<data.length && (prevLen+data[row].length+1)<selection){
            prevLen=prevLen+data[row].length+1;
            row++;
        }
        col = selection-prevLen;
        return [row,col];
    }

    setCaretLoc(row, col, len=0){
        var textArea = document.querySelector('.code-input');
        var data = textArea.value;
        data = data.split("\n");
        var prevLen = 0;
        var i=0;
        while(i<row){
            prevLen=prevLen+data[i].length+1;
            i++;
        }
        prevLen+=col;
        textArea.selectionStart=prevLen;
        textArea.selectionEnd=prevLen+len;
        console.log("row & col", row, col);
    }

    async jumpToSearchLine(listOfCursors, lastIndex, self){
        var lineNo = listOfCursors[lastIndex]['lineNo'];
        if(lineNo>(self.props.state.startingLineNo+self.props.state.data.split("\n").length) || lineNo<(self.props.state.startingLineNo)){
            var requestBody = this.packEditRequest()
            requestBody['lineNumber'] = lineNo;
            requestBody['numberOfLines'] = 50;
            var body = await jump(requestBody);
            this.applyResponse(body);

            //var content = body['lines'].join('\n');
            //self.props.setData(content, self.props.state.length);
            //self.props.updateStartingLineNo(body['startingLineNo']);
        }
        let searchLen = document.getElementById('find').value.length;
        this.setCaretLoc(lineNo-self.props.state.startingLineNo, listOfCursors[lastIndex]['columnNo'], searchLen);
    }

    async fillSearchResult(searchPattern){
        let self = this;

        var listOfCursors = await this.search(searchPattern);//this.props.state.startingLineNo, searchPattern, this.props.state.fileName);
        await listOfCursors
        console.log("cursors ", listOfCursors);
        //if(listOfCursors.status != null && listOfCursors.status !== 200) {
        //    return displayError(listOfCursors, this.props.showErrorMsg);
        //} else {
            // set page as well
            var searchResult = {
                'presentIndex': 0,
                'listOfCursors': listOfCursors
            };
            this.props.setSearchResult(searchResult);
            console.log(typeof(listOfCursors));
            if (listOfCursors.length > 0) {
                self.jumpToSearchLine(listOfCursors, 0, self);
                document.getSelection().focusNode.parentElement.scrollTop = 0;
            }
        //}
    }

    handleFindEvent(evt) {
        let findForm = document.getElementById('find');
        this.fillSearchResult(findForm.value);
    }

    packEditRequest() {
        var textAreaValue = document.querySelector(".code-input").value;
        var startCaret = this.getCaretLoc(textAreaValue, document.querySelector(".code-input").selectionStart)
        var endCaret = this.getCaretLoc(textAreaValue, document.querySelector(".code-input").selectionEnd)

        var data = textAreaValue;

        var newRequestBody = {
            'cursorStart': {
                'lineNo': startCaret[0],
                'columnNo' : startCaret[1]
            },

            'cursorEnd': {
                'lineNo' : endCaret[0],
                'columnNo' : endCaret[1]
            },
            'startingLineNo': this.props.state.startingLineNo,
            'endingLineNo': this.props.state.startingLineNo + this.props.state.length,
            'fileName' : this.props.state.fileName,
            'dataNow': data,
            'start': document.querySelector(".code-input").selectionStart,
            'end' : document.querySelector(".code-input").selectionEnd
        }

        return newRequestBody;
    }

    updatePageResponse(pageResponse) {
        var text = pageResponse['lines'].join("\n")
        this.props.setData(text, pageResponse['lines'].length);

        this.setCaretLoc(pageResponse['cursorAt']['lineNo'], pageResponse['cursorAt']['columnNo'])
    }

    applyResponse(pageResponse) {
        console.log(pageResponse);
        var text = pageResponse['lines'].join("\n")
        this.props.setData(text, pageResponse['lines'].length);
        this.props.updateStartingLineNo(pageResponse['startingLineNo'])

        var row = pageResponse['cursorAt']['lineNo'] - this.props.state.startingLineNo;
        var col = pageResponse['cursorAt']['columnNo']
        this.setCaretLoc(row, col);
    }

    async search(pattern) {
        var body = this.packEditRequest();
        var searchBody = {
            'pattern' : pattern,
        }

        var newRequestBody = Object.assign({}, body, searchBody);
        var pageResponse = await search(newRequestBody);
        //this.applyResponse(pageResponse); // FIXME
        //
        return pageResponse
    }

    async searchReplace(oldPattern, newPattern) {
        var body = this.packEditRequest();
        var searchReplaceBody = {
            'pattern' : oldPattern,
            'newPattern' : newPattern
        }

        var newRequestBody = Object.assign({}, body, searchReplaceBody);
        var pageResponse = await searchReplace(newRequestBody);
        this.applyResponse(pageResponse);
    }


    handleReplaceEvent(evt){
        let searchPattern = document.getElementById('find').value;
        let replacePattern = document.getElementById('replace').value;
        console.log("search pattern = " + searchPattern)
        console.log("replace pattern = " + replacePattern)
        this.searchReplace(searchPattern, replacePattern);
        console.log("Replace");
    }

    findAndReplaceInterface(evt){
        let checkbox = document.getElementById('replaceButton');
        if(checkbox.checked){
            this.handleReplaceEvent(evt);
        }
        else{
            this.handleFindEvent(evt);
        }
        document.getElementById('find').focus();
        evt.preventDefault();
    }

    replaceToggle(evt){
        let checkbox = document.getElementById('replaceButton');
        if(!checkbox.checked){
            document.getElementById('replace').style.display = 'none';
        }
        else{
            document.getElementById('replace').style.display = 'initial';
        }
    }

    getFileData(evt){
        var reader = new FileReader();
        var fileToRead = document.getElementById('myfile').files[0];
        setTimeout(this.handleFiles(reader, fileToRead),1000);
    }

    async handleFiles(reader, fileToRead){
        let data;
        reader.readAsText(fileToRead);
        reader.onloadend = evt => {
            data = reader.result;

            var requestBody = this.packEditRequest()
            requestBody['fileName'] = fileToRead['name'];
            requestBody['lineNumber'] = 0;
            requestBody['dataNow'] = data;
            this.props.writeFromFile(requestBody);
        }
        await this.setCaretLoc(0,0);
    }

    async handleJump(){
        let jumpPos = parseInt(document.getElementById('jump').value);
        var requestBody = this.packEditRequest();
        requestBody['lineNumber'] = jumpPos;
        requestBody['numberOfLines'] = 50;

        var body = await jump(requestBody); //jumpPos, this.props.state.fileName, this.getCaretLoc());
        this.applyResponse(body);
        //var content = body['lines'].join('\n');
        //this.props.setData(content, body['lines'].length);
        //this.props.updateStartingLineNo(body['startingLineNo']);
        // this.props.scrollToRow(jumpPos);     self.props.scrollToRow(row);
    }

    handleJumpEvent(evt){
        this.handleJump();
        evt.preventDefault();
    }

    render = () =>{
        return(
            <Nav style={{background:"rgb(30,31,38)", height:"7vh", borderBottom:"0.2px solid #555"}}>
                <Nav.Item className='p-2' style={{color:"#F4F4F4"}}>
                    QCharm
                </Nav.Item>

                <Nav.Item className='ml-auto p-1'>
                    <InputGroup size="sm" style={{zIndex:"200"}}>
                        <Form onSubmit = {this.handleJumpEvent} className="p-1">
                            <FormControl
                                id = 'jump'
                                placeholder = "Jump"
                                aria-describedby="basic-addon1"
                                style={{background:"rgb(40,40,40)",height:"4vh",padding:"0px",border:"none", color:"#fff", fontSize:"2vh"}}
                            />
                            <Button type="submit" style={{display:"none"}}></Button>
                        </Form>
                    </InputGroup>
                </Nav.Item>

                <Nav.Item className='p-1'>
                    <InputGroup id='findReplace' size="sm" style={{zIndex:"200"}}>
                        <Form onSubmit = {this.findAndReplaceInterface} className="p-1">
                            <FormControl
                                id = 'find'
                                placeholder = "Find"
                                aria-describedby="basic-addon1"
                                style={{background:"rgb(40,40,40)",height:"4vh",padding:"0px",border:"none", color:"#fff", fontSize:"2vh"}}
                            />
                            <FormControl
                                id = 'replace'
                                placeholder = "Replace"
                                aria-describedby="basic-addon1"
                                style={{background:"rgb(40,40,40)",height:"4vh",padding:"0px",border:"none", marginTop:"5px", color:"#fff", fontSize:"2vh", zIndex:"200", display:"none"}}
                            />
                            <Button type="submit" style={{display:"none"}}></Button>
                        </Form>
                        <input type="checkbox" id="replaceButton" onChange = {this.replaceToggle} style={{marginTop:"2vh"}}/>
                    </InputGroup>
                </Nav.Item>

                <Nav.Item className='p-1' style={{float:"right",padding:"0px", margin:"0px"}}>
                    <input type="file" id="myfile" name="myfile" onChange={this.getFileData} hidden/>
                    <Button size="sm" variant="outline-light" onClick={this.uploadFromFile}>Upload</Button>
                </Nav.Item>

                <Nav.Item className="p-1" style={{float:"right",padding:"0px", margin:"0px"}}>
                    <DropdownButton className="language" size="sm" title={this.props.language.toUpperCase()} variant="outline-light" onSelect={this.props.changeLan}>
                        <Dropdown.Item eventKey='c'>C</Dropdown.Item>
                        <Dropdown.Item eventKey='cpp'>C++</Dropdown.Item>
                        <Dropdown.Item eventKey='java'>Java</Dropdown.Item>
                        <Dropdown.Item eventKey='python'>Python</Dropdown.Item>
                    </DropdownButton>
                </Nav.Item>

                <Nav.Item className="p-1" style={{float:"right",padding:"0px", margin:"0px"}}>
                    <Button size="sm" variant="success" onClick={this.runCode}>Run</Button>
                </Nav.Item>
            </Nav>
        );
    }
}

export default Navbar;
